$y: 2;
$x: 0;

body {
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: #fbf8ef;
  font-family: sans-serif;
  font-size: 24px;
  font-weight: 900;
}

.game-field {
  background: #bbada0;
  border-spacing: 10px;
  border-radius: 5px;

  position: relative;
  display: grid;
  grid-template-columns: repeat(4, 20vmin);
  grid-template-rows: repeat(4, 20vmin);
  gap: 2vmin;
  padding: 2vmin;

  @media (min-width: 600px) {
    grid-template-columns: repeat(4, 15vmin);
    grid-template-rows: repeat(4, 15vmin);
  }

  @media (min-width: 800px) {
    grid-template-columns: repeat(4, 13vmin);
    grid-template-rows: repeat(4, 13vmin);
    gap: 1.5vmin;
    padding: 1.5vmin;
  }
}

.empty-cell {
  background: #d6cdc4;
  border-radius: 1vmin;
}

.full-cell {
  --y: 0;
  --x: 0;

  // background: #e47f7f;
  width: 20vmin;
  height: 20vmin;
  border-radius: 1vmin;
  color: #776e65;
  box-sizing: border-box;

  position: absolute;
  top: calc(var(--y) * (20vmin + 2vmin) + 2vmin);
  left: calc(var(--x) * (20vmin + 2vmin) + 2vmin);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 7vmin;
  transition: 100ms;
  animation: move-cell 200ms;

  @media (min-width: 600px) {
    width: 15vmin;
    height: 15vmin;
    top: calc(var(--y) * (15vmin + 2vmin) + 2vmin);
    left: calc(var(--x) * (15vmin + 2vmin) + 2vmin);
    font-size: 5.5vmin;
  }

  @media (min-width: 800px) {
    width: 13vmin;
    height: 13vmin;
    top: calc(var(--y) * (13vmin + 1.5vmin) + 1.5vmin);
    left: calc(var(--x) * (13vmin + 1.5vmin) + 1.5vmin);
    font-size: 5.5vmin;
  }

  &--2 {
    background: #eee4da;
  }

  &--4 {
    background: #ede0c8;
  }

  &--8 {
    background: #f2b179;
    color: #f9f6f2;
  }

  &--16 {
    background: #f59563;
    color: #f9f6f2;
  }

  &--32 {
    background: #f67c5f;
    color: #f9f6f2;
  }

  &--64 {
    background: #f65e3b;
    color: #f9f6f2;
  }

  &--128 {
    background: #edcf72;
    color: #f9f6f2;
  }

  &--256 {
    background: #edcc61;
    color: #f9f6f2;
  }

  &--512 {
    background: #edc850;
    color: #f9f6f2;
  }

  &--1024 {
    background: #edc53f;
    color: #f9f6f2;
  }

  &--2048 {
    background: #edc22e;
    color: #f9f6f2;
  }
}

@keyframes move-cell {
  0% {
    opacity: 0.5;
    transform: scale(0);
  }
}

.game-header {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 24px;
  padding: 10px;
  box-sizing: border-box;
}

h1 {
  background: #edc22e;
  color: #f9f6f2;
  width: 75px;
  height: 75px;
  font-size: 24px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  margin: 0;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #d6cdc4;
  width: 75px;
  height: 75px;
  border-radius: 5px;
  color: #776e65;
  box-sizing: border-box;
  font-size: 16px;
  margin: 0 8px 0 0;
}

.controls {
  display: flex;
}

.button {
  border: none;
  border-radius: 5px;
  cursor: pointer;
  color: #f9f6f2;
  font-family: sans-serif;
  font-weight: 700;
  font-size: 16px;
  width: 75px;
  height: 75px;

  transition: 0.25s ease background;
}

.start {
  background: #1dae28;
  font-size: 20px;

  &:hover {
    background: #179921;
  }
}

.restart {
  background: #f1b2b2;
  font-size: 18px;
  display: none;

  &:hover {
    background: #f87474;
  }
}

.message {
  box-sizing: border-box;
  width: 100%;
  background: #d6cdc4;
  color: #776e65;
  padding: 10px;
  text-align: center;
  border-radius: 5px;
  font-size: 20px;
}

.hidden {
  display: none;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 350px;
}

.message-win {
  background: #edc22e;
}

.message-lose {
  background: #f87474;
}

.message-container {
  width: 100%;
  height: 150px;
}
